.blocklayouts-modal {
	.components-modal__header {
		.components-modal__header-heading-container {
			width: 300px;
			margin-right: 20px;
		}
		.blocklayouts-modal__tabs {
			display: flex;
			gap: 8px;
			.blocklayouts-modal__tabs-button {
				&.is-selected {
					background-color: #f3f4f6;
					font-weight: 500;
				}
				&:focus:not(:disabled) {
					box-shadow: none;
					outline: none;
				}
			}
		}
		.blocklayouts-modal__refresh-button,
		.blocklayouts-modal__account-button {
			// background-color: #f3f4f6;
			border-radius: 8px;
		}
	}

	.components-modal__content {
		overflow: unset;
		div:has(> div.blocklayouts-modal__container) {
			height: 100%;
		}

		.blocklayouts-modal__container {
			height: 100%;
			display: flex;
			gap: 20px;
			.blocklayouts-modal__sidebar {
				width: 220px;
				padding-top: 16px;
				margin-right: 12px;
				display: flex;
				gap: 8px;
				flex-direction: column;
				.blocklayouts-modal__sidebar-buttons {
					display: flex;
					gap: 8px;
					flex-direction: column;

					.blocklayouts-modal__sidebar-button {
						&.is-selected {
							background-color: #f3f4f6;
							font-weight: 500;
						}
						&:focus:not(:disabled) {
							box-shadow: none;
							outline: none;
						}
					}
				}
			}
			.blocklayouts-modal__content {
				flex: 1;
				height: 100%;
				padding: 0;

				.dataviews-wrapper {
					container: dataviews-wrapper / inline-size;
					display: flex;
					flex-direction: column;
					font-size: 13px;
					height: 100%;
					line-height: 1.4;
					scroll-padding-bottom: 64px;
					padding: 0;

					.dataviews__view-actions {
						padding: 16px 0;
						// display: none; // TODO: look for better approach
					}
					.dataviews-view-grid {
						container-type: inline-size;
						grid-template-rows: max-content;
						grid-template-columns: repeat(3, minmax(0, 1fr));
						margin-bottom: auto;
						padding: 24px 0;
						.dataviews-view-grid__card {
							border-radius: 6px;
							border: 1px solid #e2e8f0;
							overflow: hidden;

							&:hover {
								border-color: #d1d5db;
							}
							.dataviews-view-grid__media {
								aspect-ratio: unset !important;
								&:after {
									display: none !important;
								}
								.blocklayouts-modal__pattern-preview {
									height: 280px;
									overflow: hidden;
									margin-bottom: 8px;
									display: flex;
									flex-direction: column;
									justify-content: center;
									cursor: pointer;
									margin: 0;
									border-bottom: 1px solid #e2e8f0;
									position: relative;
									background-color: #f4f4f4;
									.blocklayouts-modal__pattern-license {
										position: absolute;
										top: 12px;
										right: 12px;
										z-index: 999;
										border-radius: 8px;
										background-color: #1e1e1e;
										line-height: 1;
									}
								}
							}

							.dataviews-view-grid__title-actions {
								padding: 12px;
							}
						}
					}
				}
				.blocklayouts-modal__scrollable {
					padding-left: 4px;
				}
			}
			.blocklayouts-modal__screen-overlay {
				position: absolute;
				z-index: 999;
				top: 72px;
				right: 0;
				bottom: 0;
				left: 0;
				background-color: rgba(255, 255, 255, 0.2);
				backdrop-filter: blur(6px);
				display: flex;
				justify-content: center;
				align-items: center;
			}
		}
	}
	.blocklayouts-modal__scrollable {
		padding-right: 6px;
		margin-right: -14px;
		scrollbar-width: auto;
		-ms-overflow-style: auto;

		&::-webkit-scrollbar {
			width: 8px;
			background: transparent;
		}
		&::-webkit-scrollbar-track {
			background: transparent;
		}

		&::-webkit-scrollbar-thumb {
			background-color: rgba(255, 255, 255, 0);
		}

		// Show scrollbar on hover
		&:hover {
			&::-webkit-scrollbar-thumb {
				background-color: #7f7f7f;
			}
		}
	}
}

.blocklayouts-modal__overlay {
	z-index: 9999 !important;
}
.blocklayouts-modal__styling-content {
	min-width: 240px;
}
.blocklayouts-modal__spinner {
	display: flex;
	justify-content: center;
	height: 100%;
	align-items: center;
}

.blocklayouts-modal__filters-content {
	.components-popover__content {
		width: auto;
		min-width: 240px;
		display: flex;
		flex-direction: column;
		gap: 12px;
	}
}

// patterns preview for pages
.blocklayouts-modal.is-pages
	.components-modal__content
	.blocklayouts-modal__container
	.blocklayouts-modal__content
	.dataviews-wrapper
	.dataviews-view-grid
	.dataviews-view-grid__card
	.blocklayouts-modal__pattern-preview {
	min-height: 360px;
}
// mobile
@media (max-width: 779px) {
	.blocklayouts-modal {
		.components-modal__content {
			width: 100%;
			.blocklayouts-modal__container {
				display: block;
				.blocklayouts-modal__sidebar {
					flex-direction: row;
					gap: 16px;
					width: 100%;
					h4 {
						display: none;
					}
					.blocklayouts-modal__scrollable {
						overflow: auto hidden !important;
					}
					.blocklayouts-modal__sidebar-buttons {
						display: inline-flex;
						flex-direction: row;
						.blocklayouts-modal__sidebar-button {
							white-space: nowrap;
						}
					}
				}
				.blocklayouts-modal__content {
					.dataviews-wrapper {
						.dataviews-view-grid {
							grid-template-columns: repeat(1, minmax(0, 1fr));
						}
					}
				}
			}
		}
	}
}
@media (min-width: 780px) and (max-width: 1024px) {
	.blocklayouts-modal {
		.components-modal__content {
			.blocklayouts-modal__container {
				.blocklayouts-modal__content {
					.dataviews-wrapper {
						.dataviews-view-grid {
							grid-template-columns: repeat(2, minmax(0, 1fr));
						}
					}
				}
			}
		}
	}
}

.animate-pulse {
	animation: pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}

@keyframes pulse {
	50% {
		opacity: 0.5;
	}
}
